.users {
  &__table {
    grid-template-columns: 200px auto 200px 100px 60px;
    border: 1px solid #E3E8EE;
    overflow-y: auto;
  }

  &__filters-container {
    width: 100%;
    height: 48px;
    margin-bottom: 40px;
    display: flex;
    gap: 20px;
  }

  &__help-message {
    text-align: left;
    margin-bottom: 12px;
  }
}
